/* src/components/StatisticsModal.css */

/* Styles for the content inside the modal */
.statistics-modal .stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
    gap: 1rem;
    margin-bottom: 2rem;
}

.statistics-modal .stat-card {
    background-color: var(--tertiary-dark);
    border-radius: 8px;
    padding: 1rem;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.statistics-modal .stat-card h4 {
    margin-top: 0;
    margin-bottom: 0.5rem;
    color: var(--text-dim);
    font-size: 0.9rem;
    font-weight: normal;
}

.statistics-modal .stat-card p {
    margin: 0;
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--accent-blue);
}

.statistics-modal .history-table-container {
    overflow-x: auto;
    max-height: 40vh; /* Limit height inside modal */
}

.statistics-modal .history-table {
    width: 100%;
    border-collapse: collapse;
    background-color: var(--tertiary-dark);
}

.statistics-modal .history-table th,
.statistics-modal .history-table td {
    padding: 0.75rem 1rem;
    text-align: left;
    border-bottom: 1px solid var(--border-color);
}

.statistics-modal .history-table th {
    background-color: var(--background-dark);
    color: var(--accent-blue);
    font-weight: bold;
    position: sticky;
    top: 0;
}

.statistics-modal .history-table .signal-class-buy {
    color: var(--accent-green);
    font-weight: bold;
}

.statistics-modal .history-table .signal-class-sell {
    color: var(--accent-red);
    font-weight: bold;
}

.statistics-modal .history-table .signal-class-hold {
    color: var(--accent-yellow);
    font-weight: bold;
}

.statistics-modal .error-message {
    color: var(--accent-red);
    background-color: rgba(255, 61, 113, 0.1);
    padding: 1rem;
    border-radius: 4px;
    text-align: center;
}