/* src/components/MarketAnalysisPanel.css */

.market-analysis-panel {
    background-color: var(--tertiary-dark);
    padding: 20px;
    border-radius: var(--border-radius-main);
    border: 1px solid var(--border-color);
/* --- Styles for Buy/Sell Pressure Bar --- */
.pressure-bar-section {
    padding: 15px;
    border-top: 1px solid var(--border-color);
}

.pressure-bar-label {
    display: flex;
    justify-content: space-between;
    font-size: 0.8rem;
    color: var(--text-color-secondary);
    margin-bottom: 8px;
    font-weight: 500;
}

.pressure-bar {
    width: 100%;
    height: 10px;
    background-color: var(--sell-color-bg); /* สีพื้นหลังสำหรับแรงขาย */
    border-radius: 5px;
    overflow: hidden;
    display: flex;
}

.buy-pressure {
    height: 100%;
    background-color: var(--buy-color); /* สีสำหรับแรงซื้อ */
    border-radius: 5px 0 0 5px;
    transition: width 0.5s ease-in-out;
}
/* --- NEW: Enhanced Styles for Pressure Bar Labels --- */
.pressure-bar-label {
    display: flex;
    justify-content: space-between;
    font-size: 0.85rem; /* Slightly larger font */
    margin-bottom: 8px;
    font-weight: 600; /* Bolder font */
}

.pressure-bar-label span:first-child {
    color: var(--accent-green); /* Green color for Buy Pressure */
}

.pressure-bar-label span:last-child {
    color: var(--accent-red); /* Red color for Sell Pressure */
}

/* ถ้าแรงซื้อเต็ม 100% ให้ขอบมนทั้งสองข้าง */
.buy-pressure[style*="width: 100%"] {
    border-radius: 5px;
}
    box-shadow: 0 4px 12px var(--shadow-color);
}

.analysis-content {
    margin-bottom: 25px; /* Add space between analysis and request button section */
}

.market-analysis-panel.loading-state {
    text-align: center;
    color: var(--text-dim);
    font-style: italic;
    padding: 30px 20px;
}

.panel-title {
    margin-top: 0;
    margin-bottom: 20px;
    text-align: center;
    color: var(--accent-blue);
    font-size: 1.5em;
    font-weight: 600;
}

.analysis-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}

.analysis-card {
    background-color: var(--secondary-dark);
    padding: 15px;
    border-radius: 10px;
    text-align: center;
    border-left: 4px solid var(--border-color);
    box-shadow: inset 0 1px 3px rgba(0,0,0,0.5);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.analysis-card:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 15px rgba(0,0,0,0.2);
}

.card-label {
    font-size: 0.9em;
    color: var(--text-dim);
    margin-bottom: 10px;
    text-transform: uppercase;
}

.card-value {
    font-size: 1.8em;
    font-weight: 700;
    font-family: 'Fira Code', monospace;
}

/* Specific card styles */
.analysis-card.trend-up {
    border-left-color: var(--accent-green);
}
.analysis-card.trend-up .card-value {
    color: var(--accent-green);
}

.analysis-card.trend-down {
    border-left-color: var(--accent-red);
}
.analysis-card.trend-down .card-value {
    color: var(--accent-red);
}

.analysis-card.trend-sideways {
    border-left-color: var(--accent-yellow);
}
.analysis-card.trend-sideways .card-value {
    color: var(--accent-yellow);
}

/* Pressure Bar Styles */
.pressure-bar-container {
    display: flex;
    width: 100%;
    height: 30px;
    background-color: var(--accent-red); /* The base is red (sell) */
    border-radius: 8px;
    overflow: hidden;
    margin-top: 5px;
    font-size: 0.9em;
    font-weight: 600;
    color: var(--background-dark);
}

.pressure-bar.buy-bar {
    height: 100%;
    background-color: var(--accent-green);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: width 0.5s ease-in-out;
    border-radius: 8px;
}

/* NEW: Signal Request Section */
.signal-request-section {
    padding-top: 20px;
    border-top: 1px solid var(--border-color);
    text-align: center;
}

/* NEW: Class to remove top border when no analysis data is shown */
.signal-request-section.no-border {
    border-top: none;
    padding-top: 5px; /* Add a little space */
}

.signal-request-section p {
    color: var(--text-dim);
    margin-bottom: 15px;
    font-style: italic;
}

.signal-request-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.request-button {
    padding: 12px 25px;
    border: none;
    border-radius: 8px;
    font-size: 1em;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: uppercase;
    color: #fff;
}

.request-button.buy {
    background: linear-gradient(45deg, var(--accent-green), #00c853);
    box-shadow: 0 4px 15px rgba(0, 230, 118, 0.3);
}

.request-button.buy:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 230, 118, 0.5);
}

.request-button.sell {
    background: linear-gradient(45deg, var(--accent-red), #d81b60);
    box-shadow: 0 4px 15px rgba(255, 61, 113, 0.3);
}

.request-button.sell:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 61, 113, 0.5);
}

.request-button:disabled {
    background: var(--tertiary-dark);
    color: var(--text-dim);
    cursor: not-allowed;
    box-shadow: none;
    transform: none;
}
